:root {
    --paletteColor4: #f0cf5e;
    --paletteColor5: #eff8fd;
    --paletteColor7: #d8d8d8;
    --paletteColor8: #ff00ba;
    --paletteColor9: #fae713;
    --inputBorderStyle: solid;
    --centralColumn: 460px;
    --inputRow: 100px;

    background: var(--paletteColor3);
}

.strange-search {
    display: grid;
    grid-template-columns: 1fr 100px 460px 100px 1fr;
    grid-template-columns: 1fr 100px var(--centralColumn) 100px 1fr;
    grid-template-rows: 100px 100px;
    grid-template-rows: 100px var(--inputRow)
}

.strange-search .strange,
    .strange-search .search {
        color: #f0cf5e;
        color: var(--paletteColor4);
        font-size: 60px;
        font-weight: 800;
        margin: 0;
    }

.strange-search .strange {
        -webkit-writing-mode: vertical-lr;
                writing-mode: vertical-lr;
        grid-column: 2;
        grid-row: 2 / span 5;
        justify-self: end;
        margin-right: 30px;
    }

.strange-search .search {
        grid-column: 4 / span 2;
        grid-row: 2;
        margin-top: 10px;
        margin-left: 30px;
        text-overflow: overflow;
    }

.strange-search input {
        grid-column: 3;
        grid-row: 2;
        align-self: center;
        justify-self: center;
        width: 100%;
        padding: 20px;
        margin: 0;
        margin-bottom: 3px;
        text-align: center;
        outline: 0;
        border: 10px solid #f0cf5e;
        border: 10px var(--inputBorderStyle) var(--paletteColor4);
        background-color: transparent;
        color: #eff8fd;
        color: var(--paletteColor5);
        border-radius: 5px;
        font-size: 40px;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.12),
            inset 0 0 2px rgba(0, 0, 0, 0.19);
        z-index: 3
    }

.strange-search input::-webkit-input-placeholder {
            color: #f0cf5e;
            color: var(--paletteColor4);
        }

.strange-search input::-ms-input-placeholder {
            color: #f0cf5e;
            color: var(--paletteColor4);
        }

.strange-search input::placeholder {
            color: #f0cf5e;
            color: var(--paletteColor4);
        }

.strange-search ul {
        grid-column: 3;
        grid-row: 3;
        margin: 0;
        padding: 0;
        z-index: 2
    }

.strange-search ul li {
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            -webkit-box-pack: justify;
            -webkit-justify-content: space-between;
                    justify-content: space-between;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.14);
            margin: 0;
            padding: 20px;
            background: #eff8fd;
            background: var(--paletteColor5);
            border-bottom: 1px solid;
            border-bottom-color: var(--paletteColor2);
            cursor: pointer;
            -webkit-transition: border-bottom-color 0.3s ease;
            transition: border-bottom-color 0.3s ease
        }

.strange-search ul li span {
                font-size: 24px;
                font-weight: 200;
            }

.strange-search ul li .country {
                padding-top: 5px;
            }

.strange-search ul li .flag {
                display: grid;
                -webkit-box-align: start;
                -webkit-align-items: start;
                        align-items: start;
                justify-items: start;
                font-size: 35px;
            }

.strange-search ul li .highlight {
                background: #f0cf5e;
                background: var(--paletteColor4);
            }

.strange-search ul li:nth-child(even) {
                -webkit-clip-path: polygon(
                    1.87% 100%,
                    0.09% 0.22%,
                    97.34% -1.86%,
                    100.2% 101.69%
                );
                        clip-path: polygon(
                    1.87% 100%,
                    0.09% 0.22%,
                    97.34% -1.86%,
                    100.2% 101.69%
                );
            }

.strange-search ul li:nth-child(odd) {
                -webkit-clip-path: polygon(
                    0.19% 101.68%,
                    1.96% 0.23%,
                    100.14% -6.07%,
                    97.59% 101.68%
                );
                        clip-path: polygon(
                    0.19% 101.68%,
                    1.96% 0.23%,
                    100.14% -6.07%,
                    97.59% 101.68%
                );
            }

.strange-search ul li:nth-child(1) {
                -webkit-clip-path: polygon(
                    0.19% 101.68%,
                    0.09% -4.35%,
                    100.14% -6.07%,
                    97.4% 101.68%
                );
                        clip-path: polygon(
                    0.19% 101.68%,
                    0.09% -4.35%,
                    100.14% -6.07%,
                    97.4% 101.68%
                );
            }

.strange-search ul li:hover {
                background: #d8d8d8;
                background: var(--paletteColor7);
                border-bottom-color: #f0cf5e;
                border-bottom-color: var(--paletteColor4);
            }

.strange-search b {
        position: fixed;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 20vh;
        background: #f0cf5e;
        background: var(--paletteColor4);
        -webkit-clip-path: polygon(
            0.19% 101.68%,
            0.27% 54.65%,
            100.05% -1.07%,
            100% 100%
        );
                clip-path: polygon(
            0.19% 101.68%,
            0.27% 54.65%,
            100.05% -1.07%,
            100% 100%
        );
    }

.strange-search .modal {
        position: fixed;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        height: 100%;
        width: 100%;
        z-index: 3;
        background-color: rgba(0, 0, 0, 0.72);
        -webkit-perspective: 1000px;
                perspective: 1000px
    }

.strange-search .modal .tilt {
            position: relative;
            grid-column: 2;
            grid-row: 2;
            width: 650px !important;
            margin: 0 !important;
            padding: 7px !important;
            background-image: -webkit-linear-gradient(
                315deg,
                #ff00ba 0%,
                #fae713 100%
            ) !important;
            background-image: linear-gradient(
                135deg,
                #ff00ba 0%,
                #fae713 100%
            ) !important;
            background-image: -webkit-linear-gradient(
                315deg,
                var(--paletteColor8) 0%,
                var(--paletteColor9) 100%
            ) !important;
            background-image: linear-gradient(
                135deg,
                var(--paletteColor8) 0%,
                var(--paletteColor9) 100%
            ) !important;
            font-size: 30px !important;
            z-index: 5 !important
        }

.strange-search .modal .tilt .content {
                background-color: var(--paletteColor3) !important;
                color: #eff8fd;
                color: var(--paletteColor5);
                height: 100%;
                padding: 15px
            }

.strange-search .modal .tilt .content h1 {
                    margin-top: 0;
                    color: #eff8fd;
                    color: var(--paletteColor5);
                }

.strange-search .modal .tilt .content ul li {
                        -webkit-animation: none 0s ease 0s 1 normal none running;
                                animation: none 0s ease 0s 1 normal none running;
                        -webkit-backface-visibility: visible;
                                backface-visibility: visible;
                        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
                        border: medium none currentColor;
                        border-collapse: separate;
                        -webkit-border-image: none;
                                border-image: none;
                        border-radius: 0;
                        border-spacing: 0;
                        bottom: auto;
                        box-shadow: none;
                        box-sizing: content-box;
                        caption-side: top;
                        clear: none;
                        clip: auto;
                        color: #000;
                        -webkit-columns: auto;
                                columns: auto;
                        -webkit-column-count: auto;
                                column-count: auto;
                        -webkit-column-fill: balance;
                                column-fill: balance;
                        grid-column-gap: normal;
                        -webkit-column-gap: normal;
                                column-gap: normal;
                        -webkit-column-rule: medium none currentColor;
                                column-rule: medium none currentColor;
                        -webkit-column-span: 1;
                                column-span: 1;
                        -webkit-column-width: auto;
                                column-width: auto;
                        content: normal;
                        counter-increment: none;
                        counter-reset: none;
                        cursor: auto;
                        direction: ltr;
                        display: inline;
                        empty-cells: show;
                        float: none;
                        font-family: serif;
                        font-size: medium;
                        font-style: normal;
                        -webkit-font-feature-settings: normal;
                                font-feature-settings: normal;
                        font-variant: normal;
                        font-weight: normal;
                        font-stretch: normal;
                        line-height: normal;
                        height: auto;
                        -webkit-hyphens: none;
                            -ms-hyphens: none;
                                hyphens: none;
                        left: auto;
                        letter-spacing: normal;
                        list-style: disc outside none;
                        margin: 0;
                        max-height: none;
                        max-width: none;
                        min-height: 0;
                        min-width: 0;
                        opacity: 1;
                        orphans: 2;
                        outline: medium none invert;
                        overflow: visible;
                        overflow-x: visible;
                        overflow-y: visible;
                        padding: 0;
                        page-break-after: auto;
                        page-break-before: auto;
                        page-break-inside: auto;
                        -webkit-perspective: none;
                                perspective: none;
                        -webkit-perspective-origin: 50% 50%;
                                perspective-origin: 50% 50%;
                        position: static;
                        right: auto;
                        -moz-tab-size: 8;
                             tab-size: 8;
                        table-layout: auto;
                        text-align: left;
                        text-align-last: auto;
                        text-decoration: none;
                        text-indent: 0;
                        text-shadow: none;
                        text-transform: none;
                        top: auto;
                        -webkit-transform: none;
                                transform: none;
                        -webkit-transform-origin: 50% 50% 0;
                                transform-origin: 50% 50% 0;
                        -webkit-transform-style: flat;
                                transform-style: flat;
                        -webkit-transition: none 0s ease 0s;
                        transition: none 0s ease 0s;
                        unicode-bidi: normal;
                        vertical-align: baseline;
                        visibility: visible;
                        white-space: normal;
                        widows: 2;
                        width: auto;
                        word-spacing: normal;
                        z-index: auto;
                        all: initial;
                        color: #eff8fd;
                        color: var(--paletteColor5);
                        display: block;
                        font-size: 24px;
                        font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif
                    }

.strange-search .modal .tilt .content ul li span:first-child {
                                color: #d8d8d8;
                                color: var(--paletteColor7);
                                margin-right: 7px;
                                font-weight: 100;
                            }

.strange-search .modal .tilt .content ul li:not(:last-child) {
                            margin-bottom: 7px;
                        }

.strange-search .modal .tilt .close {
                position: absolute;
                right: 35px;
                top: 35px;
                width: 32px;
                height: 32px;
                opacity: 0.3;
                cursor: pointer
            }

.strange-search .modal .tilt .close:hover {
                    opacity: 1;
                }

.strange-search .modal .tilt .close:before,
                .strange-search .modal .tilt .close:after {
                    position: absolute;
                    left: 15px;
                    content: ' ';
                    height: 33px;
                    width: 2px;
                    background-color: #eff8fd;
                    background-color: var(--paletteColor5);
                }

.strange-search .modal .tilt .close:before {
                    -webkit-transform: rotate(45deg);
                            transform: rotate(45deg);
                }

.strange-search .modal .tilt .close:after {
                    -webkit-transform: rotate(-45deg);
                            transform: rotate(-45deg);
                }

@media (min-width: 769px) and (max-width: 1024px) {

.strange-search {
        --centralColumn: 400px;
        --inputRow: 80px
}

        .strange-search .strange,
        .strange-search .search {
            font-size: 50px;
        }

        .strange-search .strange {
            margin-right: 10px;
        }

        .strange-search .search {
            margin-left: 10px;
        }

        .strange-search input {
            padding: 15px;
            border: 7px solid #f0cf5e;
            border: 7px var(--inputBorderStyle) var(--paletteColor4);
            font-size: 35px;
        }
    }

@media (min-width: 668px) and (max-width: 768px) {

.strange-search {
        --centralColumn: 350px;
        --inputRow: 70px;
        grid-template-columns: 1fr 70px 460px 70px 1fr;
        grid-template-columns: 1fr 70px var(--centralColumn) 70px 1fr
}

        .strange-search .strange,
        .strange-search .search {
            font-size: 40px;
        }

        .strange-search .strange {
            margin-right: 5px;
        }

        .strange-search .search {
            margin-left: 5px;
        }

        .strange-search input {
            padding: 15px;
            border: 7px solid #f0cf5e;
            border: 7px var(--inputBorderStyle) var(--paletteColor4);
            font-size: 24px;
        }
            .strange-search ul li {
                padding: 15px
            }

                .strange-search ul li span {
                    font-size: 20px;
                }
    }

