            font-size: 50px;
            display: grid;
        }

        .strange-search .strange {
            grid-row: 2;
            justify-self: center;
            -webkit-writing-mode: initial;
                    writing-mode: initial;
            margin: 0;
        }

        .strange-search .search {
            grid-row: 3;
            justify-self: center;
            margin: 0;
        }

        .strange-search input {
            grid-row: 5;
            font-size: 25px;
            border-width: 5px;
        }

        .strange-search ul {
            grid-row: 6
        }
                .strange-search ul li span {
                    font-size: 18px;
                }

                .strange-search ul li .flag {
                    font-size: 24px;
                }
            .strange-search .modal .tilt {
                width: 400px !important;
                z-index: 5 !important
            }

                .strange-search .modal .tilt .content {
                    background-color: var(--paletteColor3) !important;
                    color: var(--paletteColor3);
                    height: 100%;
                    padding: 15px
                }

                    .strange-search .modal .tilt .content h1 {
                        margin-top: 0;
                        margin-bottom: 10px;
                        font-size: 35px;
                    }
                            .strange-search .modal .tilt .content ul li span {
                                font-size: 14px;
                                margin-bottom: 0px;
                            }
    }

@media (max-width: 414px) {

.strange-search {
        --centralColumn: 280px
}

        .strange-search input {
            font-size: 20px;
        }
            .strange-search .modal .tilt {
                width: 280px !important;
            }
    }
